.menu {
  font-size: 14px;
  font-weight: 400;
  line-height: 100%;
  overflow: hidden;

  @include onTablet {
    border-top: 1px solid $color-secondary;
    grid-column: -2/-6;
  }

  @include onDesktop {
    border-top: none;
  }

  &__content {
    @include grid;

    &--header {
      transform: translateX(52px);
      margin-bottom: 0;

      @include onTablet {
        transform: translateX(0);
      }
    }

    @include onDesktop {
      position: absolute;
      top: 0;
      right: 200px;
      display: flex;
      gap: 100px;
    }
  }

  &__box {
    display: grid;
    row-gap: 20px;

    @include onTablet {
      &:first-child {
        grid-column: 2/4;
      }
    }
  }

  &__item {
    color: $color-secondary;
    display: block;
    text-decoration: none;
    transition: color 0.5s;

    @include hover (color, $color-hover);

    &--header {
      margin: 0;
      color: #fff;
      font-size: 16px;
      font-weight: 500;
      line-height: 100%;
      font-family: "IBM Plex Sans", sans-serif;

      @include onTablet {
        font-size: 22px;
        line-height: 22px;
        font-weight: 550;

        padding-inline-end: 20px;
        margin-right: -65px;
      }
    }
  }
}
